import interfaces from '../../utils/interfaces.js';
import app from "../../App.vue";
export default {
	data() {
		return {
			article: '',
		}
	},
	onLoad(options) {
		var id = options.id ? options.id : 1;
		this.loadData(id);
	},
	methods: {
		loadData(id) {
			this.request({
				url: interfaces.articleDetail,
				data: {
					id: id
				},
				success: res => {
					this.article= res.data;
				}
			})
		},
	}
}
